<template>
  <div class="mian-container">
    <content-header></content-header>
    <a-row>
      <!--  -->
      <a-col :span="4" class="aside-left">
        <div class="aside-show">
          <div class="plate-show">
              <mailtemplate :toSon="setData" v-show="showtemplate"></mailtemplate>
                <!-- <templatesecond v-show="showtemplateSec"></templatesecond> -->
               <!-- <templatethird v-show="showtemplateThi"></templatethird> -->
          </div>
        </div>

        <div class="aside-footer">
          <a-button @click="clickFade">
            <a-icon type="plus" style="margin-left:20px ;font-size=:16px"></a-icon>
          </a-button>
          <div class="choose-plate" v-show="tempalteIsShow">
            <div class="plate1 plate" @click="chooseTemplate(1)">
              <mailtemplate  ></mailtemplate>
              
            </div>
            <div class="plate2 plate" @click="chooseTemplate(2)">
              <!-- <templatesecond ></templatesecond> -->
            </div>
            <div class="plate3 plate" @click="chooseTemplate(3)">
              <!-- <templatethird  ></templatethird> -->
            </div>
          </div>
        </div>
      </a-col>
      <!--  -->
      <a-col :span="14" class="center-container">
        <p class="center-text"></p>
        <mailtemplate  v-show="showtemplate"></mailtemplate>
        
         <!-- <templatesecond v-show="showtemplateSec"></templatesecond> -->
         <!-- <templatethird v-show="showtemplateThi"></templatethird> -->

        
      </a-col>
      <!--  -->
      <a-col :span="6" class="aside-right">
        
        <div class="setting">
          <a-row>
            <a-col :span="8" class="setting-col col-active">
              <a-icon type="setting" class="setting-icon"></a-icon>
            </a-col>
            <a-col :span="8" class="setting-col">
              <a-icon type="setting" class="setting-icon"></a-icon>
            </a-col>
            <a-col :span="8" class="setting-col">
              <a-icon type="setting" class="setting-icon"></a-icon>
            </a-col>
          </a-row>
        </div>
              <!-- ...... -->
        <div class="father">
          <div class="mask" v-show="bPreview" @click="setMaskShow" ></div>
          <div class="child" id="child" v-show="bPreview">
            <mailtemplate :toSon="setData"></mailtemplate>
          </div>
        </div>
        <!-- Logo.......... -->
        <div class="content">
          <a-collapse defaultActiveKey="1" :bordered="false">
            <a-collapse-panel>
              <template slot="header">logo</template>

              <div class="content">
                <a-row>
                  <a-col :span="3" offset="1">logo:</a-col>
                  <a-col :span="12" offset="1">
                    <a-upload
                      name="file"
                      :multiple="true"
                      action="//jsonplaceholder.typicode.com/posts/"
                      @change="upLoadImg"
                      :showUploadList="false"
                        
                     
                    >
                      <a-button style="width:160px; height:35px"  @click="upLoadImgUrl(1)">
                        <a-icon type="upload"/>
                      </a-button>
                    </a-upload>
                  </a-col>
                  <a-col :span="5" offset="2">
                    <a-row>
                      <a-col>
                        <a-icon type="align-left"></a-icon>
                        <a-icon type="align-center"></a-icon>
                        <a-icon type="align-right"></a-icon>
                      </a-col>
                    </a-row>
                  </a-col>
                </a-row>
              </div>
            </a-collapse-panel>
          </a-collapse>
        </div>

        <!-- ................... -->
        <div class="content">
          <a-collapse defaultActiveKey="1" :bordered="false">
            <a-collapse-panel>
              <template slot="header"></template>

              <div class="content">
                <a-row>
                  <a-col :span="6" offset="1">
                    <span></span>
                  </a-col>
                  <a-col>
                    <a-radio-group :defaultValue="1">
                      <a-radio :value="1" @click="changeHeading(1)"></a-radio>
                      <a-radio :value="2" @click="changeHeading(0)"></a-radio>
                    </a-radio-group>
                    <a-input
                      placeholder=""
                      v-model="setData.mainHeading"
                      v-show="setData.mainHeadingInputShow"
                      style="height:40px"
                    ></a-input>
                    <a-upload
                      action="//jsonplaceholder.typicode.com/posts/"
                      v-show="setData.mainHeadingUploadShow"
                      @change="upLoadImg"
                      :showUploadList="false"
                      
                    >
                      <a-input
                        placeholder=""
                        style="margin-left:86px ;width:206px;height:40px"
                         @click="upLoadImgUrl(2)"
                      >
                        <a-icon slot="suffix" type="picture"/>
                      </a-input>
                    </a-upload>
                  </a-col>
                  <a-row style="margin-top:10px">
                    <a-col :span="7"></a-col>
                    <a-col :span="15">
                      <a-upload
                        name="file"
                        :multiple="true"
                        action="//jsonplaceholder.typicode.com/posts/"
                        @change="upLoadImg"
                         :showUploadList="false"
                       
                      >
                        <a-button style="width:207px"  @click="upLoadImgUrl(3)">
                          <a-icon type="upload"/>
                        </a-button>
                      </a-upload>
                    </a-col>
                  </a-row>
                </a-row>
              </div>
            </a-collapse-panel>
          </a-collapse>
        </div>

        <!-- ................... -->
        <div class="content">
          <a-collapse defaultActiveKey="1" :bordered="false">
            <a-collapse-panel>
              <template slot="header"></template>

              <div class="content">
                <a-row>
                  <a-col :span="6" offset="1">
                    <span>
                      <i style="color:red"></i>:
                    </span>
                  </a-col>
                   <a-col>
                    <a-radio-group :defaultValue="1">
                      <a-radio :value="1" @click="changeHeading(1)"></a-radio>
                      <a-radio :value="2" @click="changeHeading(0)"></a-radio>
                    </a-radio-group>
                    <a-input
                      placeholder=""
                      v-model="setData.mainHeading"
                      v-show="setData.mainHeadingInputShow"
                      style="height:40px"
                    ></a-input>
                    <a-upload
                      action="//jsonplaceholder.typicode.com/posts/"
                      v-show="setData.mainHeadingUploadShow"
                      @change="upLoadImg"
                      :showUploadList="false"
                      
                    >
                      <a-input
                        placeholder=""
                        style="margin-left:86px ;width:206px;height:40px"
                         @click="upLoadImgUrl(2)"
                      >
                        <a-icon slot="suffix" type="picture"/>
                      </a-input>
                    </a-upload>
                  </a-col>
                  <a-row style="margin-top:20px">
                    <a-col :span="7"></a-col>
                    <a-col :span="15">
                      <a-upload
                        name="file"
                        :multiple="true"
                        action="//jsonplaceholder.typicode.com/posts/"
                        @change="upLoadImg"
                          :showUploadList="false"
                       
                      >
                        <a-button style="width:207px"  @click="upLoadImgUrl(4)">
                          <a-icon type="upload"/>
                        </a-button>
                      </a-upload>
                    </a-col>
                  </a-row>
                </a-row>
              </div>
            </a-collapse-panel>
          </a-collapse>
        </div>

        <!-- ...................... -->
        <div class="content">

          <a-collapse defaultActiveKey="1" :bordered="false">
            <a-collapse-panel>
              <template slot="header"></template>

              <div class="content">
                <a-row>
                  <a-col :span="5" offset="1">
                    <span>
                      <i style="color:red"></i>:
                    </span>
                  </a-col>
                  <a-col :span="16"offset="2">
                  <a-icon    style="margin-left:10px;"  type="bold"  />
                   <a-icon   style="margin-left:10px;"  type="italic" />
                   <a-icon   style="margin-left:10px;"  type="underline" />
                   <a-icon   style="margin-left:10px;"  type="strikethrough" />
                   <a-icon   style="margin-left:10px;"  type="align-left" />
                   <a-icon   style="margin-left:10px;"  type="align-center" />
                   <a-icon   style="margin-left:10px;"  type="align-right" />
                
                  </a-col>
                     <a-textarea
                      placeholder=""
                      :rows="4"
                      v-model="setData.textarea"
                      maxlength="100"
                      style="width:300px;margin-top:10px "
                      
                      
                    />
                  <a-row style="margin-top:20px">
                    <a-col :span="7"></a-col>
                    <a-col :span="15">
                      <a-upload
                        name="file"
                        :multiple="true"
                        action="//jsonplaceholder.typicode.com/posts/"
                         @change="upLoadImg"
                           :showUploadList="false"
                      >
                        <a-button style="width:207px"@click="upLoadImgUrl(5)">
                          <a-icon type="upload"/>
                        </a-button>
                      </a-upload>
                    </a-col>
                  </a-row>
                </a-row>

              </div>

            </a-collapse-panel>
          </a-collapse>
        </div>
        
        
        <!-- ................ -->
        <div class="content">
          <a-collapse defaultActiveKey="1" :bordered="false">
            <a-collapse-panel>
              <template slot="header"></template>

              <div class="content">
                <p></p>

                 
                <a-row style="margin-top:20px;">
                  <a-col :span="4" offset="3">
                    <span></span>
                  </a-col>
                  <a-col :span="16">
                    <a-upload
                      name="file"
                      :multiple="true"
                      action="//jsonplaceholder.typicode.com/posts/"
                      @change="upLoadImg"
                      :showUploadList="false"
                     
                    >
                      <a-button style="width:207px" @click="upLoadImgUrl(6)">
                        <a-icon type="upload"/>
                      </a-button>
                    </a-upload>
                  </a-col>
                </a-row>
                <a-row>
                  <a-col offset="14">
                    <a-checkbox-group  >
                      <a-checkbox value="A" @change="bgsetting"></a-checkbox>
                    </a-checkbox-group>
                  </a-col>
                </a-row>

                <div class="content" v-show="bgShow">
                  <a-row>
                    <a-col :span="6" offset="1">:</a-col>
                    <a-col :span="6">
                      <el-color-picker size="medium" @change="changecolor"></el-color-picker>
                    </a-col>
                    <a-col :span="11">
                      <div v-for="(item,index) in chooseColor">
                        <div class="color-item" @click="getcolor" v-bind:style="{backgroundColor:item.color}" :key="index"></div>
                     
                      </div>
                    </a-col>
                  </a-row>
                </div>
              </div>
            </a-collapse-panel>
          </a-collapse>
        </div>
       
      </a-col>
    </a-row>
  </div>
</template>
<script>
// console.log($)
import { mapGetters, mapMutations } from "vuex";
import ContentHeader from "../base/content-header";

import templatesecond from "../base/appvideo/video-template-second.vue";
import templatethird from "../base/appvideo/video-template-third.vue";
import scancodetemplate from "../base/appscancode/scancode-template.vue" ;
import mailtemplate from '../base/appmail/mailtemplate.vue'

export default {
  name: "AppMailLetter",
  data: function() {
    return {
      chooseColor:[
        {color:'#ffffff'},
        {color:'#ff7171'},
        {color:'#ffaa71'},
        {color:'#ffe071'},
        {color:'#71ff88'},
        {color:'#71ffeb'},
        {color:'#719bff'},
        {color:'#ac92f3'},
        {color:'#9b9b9b'},
        {color:'#000000'},
      ],
      setData: {
        mainHeading: "",
        textarea: 'hahahaha ',
        mainHeadingInputShow: true,
        mainHeadingUploadShow: false,

        imgList: {
          logourl: require("../assets/video/logo.png"),
          headingurl: require("../assets/video/title.png"),
          headingbg: '',
          coverurl: require("../assets/video/video.png"),
          bgurl: '',
          bgcolor:'',
          textbg:'',
          value: 0,
          chooseColor:''
         
        }
      },

      
      tempalteIsShow: false,
      bgShow: false,
      showtemplate:true,
      showtemplateSec:false,
      showtemplateThi:false


    };
  },

  
  created() {},

  methods: {
    stop(){
        var mo=function(e){e.preventDefault();};
        document.body.style.overflow='hidden';
        document.addEventListener("touchmove",mo,false);//
      },
      /******/
      move(){
        var mo=function(e){e.preventDefault();};
        document.body.style.overflow='';//
        document.removeEventListener("touchmove",mo,true);
        },//
    setMaskShow() {
     
    
      let bNewPreview = !this.bPreview;
      this.setPreview(bNewPreview);
    
      
    },

    // 
    clickFade: function() {
      this.tempalteIsShow = !this.tempalteIsShow;

    },
    // 
    chooseTemplate:function(value){
      
      if(value===1){
     
        this.showtemplate=true;
        this.showtemplateSec=false;
        this.showtemplateThi=false;
      
      }else if (value===2){
        this.showtemplateSec=true;
        this.showtemplate=false;
        this.showtemplateThi=false;

      }else{
        this.showtemplateThi=true;
        this.showtemplate=false;
        this.showtemplateSec=false;
      }
    },

    // 
    changeHeading: function(value) {
      
      if (value === 1) {
        this.setData.mainHeadingInputShow = true;
        this.setData.mainHeadingUploadShow = false;
      } else {
        this.setData.mainHeadingInputShow = false;
        this.setData.mainHeadingUploadShow = true;
      }
    },

  // 
    upLoadImgUrl: function(value) {
     
      this.setData.imgList.value = value;
      // console.log(this.setData.imgList.value)
    },
    upLoadImg: function(info) {
      
//  
      if (this.setData.imgList.value === 1) {
        this.setData.imgList.logourl = require("../assets/video" + info.file.name);
// 
      } else if (this.setData.imgList.value === 2) {
        this.setData.imgList.headingurl = require("../assets/video" + info.file.name);
// 
      } else if (this.setData.imgList.value === 3) {
        this.setData.imgList.headingbg = info.file.name;
// 
      } else if (this.setData.imgList.value === 4) {
         this.setData.imgList.coverurl = require("../assets/video" + info.file.name);

// 
      }  else if (this.setData.imgList.value === 5) {
         this.setData.imgList.textbg = info.file.name;

      }
// 
      else {
         this.setData.imgList.bgurl = info.file.name;
         this.setData.imgList.bgcolor="";
         this.setData.imgList.chooseColor="";
      }
    },
// 
    bgsetting: function() {
      this.bgShow = !this.bgShow;
       this.setData.imgList.bgurl=""
       this.setData.imgList.chooseColor="";
    },
    // 
    changecolor: function(event) {
      this.setData.imgList.bgcolor=event
      },
  getcolor:function(e){

    this.setData.imgList.chooseColor =e.target.style.backgroundColor
    this.setData.imgList.bgurl="";
    this.setData.imgList.bgcolor="";


},

    





    ...mapMutations({ setPreview: "setPreview" })
  },
  computed: {
    ...mapGetters(["bPreview"])
  },
  components: { ContentHeader,templatesecond, templatethird,mailtemplate }
};
</script>
<style scoped>
/* mian */
.mian-container {
  margin: 0 auto;
  width: 1440px;
  height: 800px;
  box-shadow: 1px 1px 10px gray;
}
.aside-left {
  box-shadow: 1px 1px 5px gray;
  height: 760px;
}
.aside-show {
  width: 100%;

  height: 90%;
}
.plate-show {
  width: 100px;
  height: 200px;

  margin: 0 auto;
  position: relative;
}
.plate-show div {
  position: absolute;
  left: -80px;
  top: -65px;

  width: 200px;
  height: 200px;
  -webkit-transform: scale(0.25);
}

.choose-plate {
  width: 100%;
  height: 400px;
  border-top: 1px solid gray;
  margin-top: -400px;
}
.plate {
  float: left;
  width: 100px;
  height: 180px;
  border: 1px solid;
  margin-top: 10px;
  margin-left: 10px;
}
.aside-footer {
  text-align: center;
  bottom: 0px;
  height: 10%;
  width: 100%;
}
.aside-footer button {
  font-size: 18px;
  font-weight: bold;
  position: absolute;
  left: 50%;
  margin-left: -65px;
  bottom: 0px;
}
.plate1.plate {
  position: relative;
}
.plate1.plate div {
  -webkit-transform: scale(0.25);
  position: absolute;
  top: -245px;
  left: -138px;
}
.plate2.plate {
  position: relative;
}
.plate2.plate div {
  -webkit-transform: scale(0.25);
  position: absolute;
  top: -240px;
  left: -138px;
}
.plate3.plate {
  position: relative;
}
.plate3.plate div {
  -webkit-transform: scale(0.25);
  position: absolute;
  top: -240px;
  left: -138px;
}

/* center */
.center-text {
  padding-top: 10px;
  font-size: 10px;
  color: gray;
}
/* right */
.aside-right {
  height: 760px;
  position: relative;

  overflow-y: scroll;
}
.setting {
  width: 100%;
  height: 46px;
  font-size: 16px;

  border-bottom: 1px solid rgba(128, 128, 128, 0.445);
}
.setting-col {
  padding-top: 10px;
  height: 46px;
}
.col-active {
  color: #1890ff;
  border-bottom: 1px solid #1890ff;
}
.setting-icon {
  margin-left: 5px;
}
.content {
  text-align: left;
  margin: 0 auto;
  height: auto;
  margin-top: 10px;
  width: 100%;
}
.logo {
  height: 100px;
  text-align: left;
}
.color-choose {
  width: 140px;
  height: 36px;
}
.color-item {
  width: 15px;
  height: 15px;

  margin: 1px 6px;
  float: left;
}
.father {
  width: 100%;
}
.mask {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: #000;
  opacity: 0.3;
  z-index: 998;
}
button {
  width: 100px;
  height: 40px;
  line-height: 40px;
  text-align: center;
}
.child {
  position: fixed;
  width: 400px;
  height: 400px;
  /* border: 1px solid #ccc; */
  text-align: center;
  /* line-height: 400px; */
  top: calc(50% - 437px);
  left: calc(50% - 259px);
  /* background: #fff; */
  /* top:90px;
    left: 442px; */
  z-index: 999;
}
</style>








