<template>
<div class="appdetail">
  <div class="appdetail-body">
    <div class="appdetail-head">
      <div class="tovertical appdetail-img">
        <img :src=oAppData.url>
      </div>
      <div class="tovertical appdetail-descript">
        <p class="app-name">{{oAppData.name}}</p>
        <p class="app-controduce">{{oAppData.descript}}</p>
      </div>
      <div class="tovertical toright">
        <p class="appdetail-bt" @click="fToEdit"></p>
      </div>
    </div>
    <p class="appdetail-more">
      <span class="more-type">:
        <span></span>
      </span>
      <span class="more-time">:
        <span>20181228</span>
      </span>
      <span class="more-score">:
        <span>4.8</span>
      </span>
    </p>
    <div class="appdetail-look">
      <p class="look-name"></p>
      <div>
        <img class="look-pic look-picleft" src="../../assets/app/appdetail1.jpg">
        <img class="look-pic look-picright" src="../../assets/app/appdetail2.png">
      </div>
    </div>
    <!-- <div class="appdetail-comment">
      <p class="comment-head">
        <span class="comment-headname"></span>
        <a-rate :defaultValue="3.5" allowHalf disabled></a-rate>
      </p>
      <div class="comment-content">
        <p class="comment-user">
          <span></span>
          <span class="comment-username">123456788</span>
          <span class="comment-time">20181228</span>
        </p>
        <p class="comment-detail">
          ............
        </p>
      </div>
      <p class="comment-more"></p>
    </div> -->
  </div>
</div>
</template>

<script>
export default {
  name: 'AppDetail',
  data () {
    return {
      oAppData: {}
    }
  },
  created () {
    this.oAppData = this.$route.params.oAppData
  },
  methods: {
    fToEdit () {
      if (this.oAppData.name === '') {
        this.$router.push({name: 'GenerateVote'})
      } else if (this.oAppData.name === '') {
        this.$router.push({name: 'APPVideo'})
      } else if (this.oAppData.name === '') {
        this.$router.push({name: 'scancode'})
      } else if (this.oAppData.name === '') {
        this.$router.push({name: 'AppMailLetter'})
      } else {
        console.log(',')
      }
    }
  }
}
</script>

<style scoped>
/* ,*/
.appdetail{ width: 1160px; margin: auto;}
.appdetail-body{ width: 750px; margin: auto;}
.appdetail-head{
  display: table;
  width: 750px; height: 100px;
}
.tovertical{
  display: table-cell; vertical-align: middle;
}
.appdetail-img{
  width: 96px; height: 96px; text-align: center;
}
.appdetail-img img{ width: 96px; height: 96px;}
.appdetail-descript{
  width: 280px;
  padding-left: 30px;
  text-align: left;
}
.appdetail-descript p{ width: 250px;}
.toright{ float: right;}
.appdetail-bt{
  margin: 20px 0; padding: 5px 10px;
  width: 85px;
  cursor: pointer;
}
/**/
.app-name{
  margin-bottom: 6px;
  font-size: 18px; line-height: 22px; font-weight: bold; color: rgba(0,0,0,0.85);
}
.app-controduce{
  margin: 0;
  font-size: 14px; line-height: 20px; color: #919191;
}
.appdetail-bt{
  font-size: 16px;
  border-radius: 4px;
  background-color: #1890FF; color: #fff;
}
/**/
.appdetail-more{
  margin: 15px 0; padding: 16px 0;
  border: 1px solid #d9d9d9; border-width: 1px 0;
  font-size: 14px; color: #919191;
}
.more-type{ float: left;}
.more-score{ float: right;}
/**/
.appdetail-look{
  text-align: left; padding-bottom: 15px;
  border-bottom: 1px solid #D9D9D9;;
}
.look-name{
  font-size: 18px; color: #919191;
  line-height: 22px;}
.look-pic{
  width: 330px; height: 220px;
  border-radius: 10px;
}
.look-picright{ float: right;}
/**/
.appdetail-comment{
  text-align: left;
  padding: 16px 0;
  color: #919191;
}
.comment-headname{
  font-size: 18px; line-height: 22px;
}
.comment-content{
  padding: 14px 30px 30px 30px;
  border-radius: 8px;
  font-size: 14px; line-height: 20px;
  background-color:  #F5F5F5;
}
.comment-user{ margin-bottom: 6px;}
.comment-username{ color: #101010;}
.comment-time{ float: right;}
.comment-more{
  margin-top: 8px;
  text-align: center;
  font-size: 14px; color: #1890FF;
  cursor: pointer;
}
</style>



