import Vue from 'vue'
import Router from 'vue-router'
// 
import CenterApp from '@/components/CenterApp'
import AppStore from '@/components/AppStore'
import AppDetail from '@/base/centerapp/AppDetail'
// 
import GenerateVote from '@/components/GenerateVote'
import VoteBasesetting from '@/base/generatevote/vote-basesetting'
import VoteConsulthelp from '@/base/generatevote/vote-consulthelp'

import APPVideo from '@/components/APPVideo'
import videopalet from '@/base/appvideo/videopalet'
import templatethird from '@/base/appvideo/video-template-third'
import templatesecond from '@/base/appvideo/video-template-second'
import templateblank from '@/base/appvideo/video-template-blank'
import scancode from '@/components/AppScanCode'
import scancodetemplate from '@/base/appscancode/scancode-template'
import AppMailLetter from '@/components/AppMailLetter'
import mailtemplate from '@/base/appmail/mailtemplate'



import qarelease from '@qa/views/qa-release'

Vue.use(Router)

export default new Router({
  routes: [
    // 
    {
      path: '/',
      redirect: '/centerapp'
    },
    /*  */
    {
      path: '/centerapp',
      name: 'CenterApp',
      component: CenterApp
    },
    {
      path: '/appstore',
      name: 'AppStore',
      component: AppStore
    },
    {
      path: '/appdetail',
      name: 'AppDetail',
      component: AppDetail
    },
    /*  */
    {
      path: '/generatevote',
      name: 'GenerateVote',
      component: GenerateVote,
      redirect: '/generatevote/basesetting',
      children: [
        {
          path: 'basesetting',
          name: 'VoteBasesetting',
          component: VoteBasesetting
        },
        {
          path: 'consulthelp',
          name: 'VoteConsulthelp',
          component: VoteConsulthelp
        }
      ]
    },
    {
      path: '/appvideo',
      name: 'APPVideo',
      component: APPVideo,
      children: [
        {
          path: 'videopalet',
          name: 'videopalet',
          component: videopalet
        },
        {
          path: 'video-template-third',
          name: 'templatethird',
          component: templatethird
        },
        {
          path: ' video-template-second',
          name: 'templatesecond',
          component: templatesecond
        },
        {
          path: 'video-template-blank',
          name: 'templateblank',
          component: templateblank
        }
      ]
    },
    {
      path: '/appscancode',
      name: 'scancode',
      component: scancode,
      children: [
        {
          path: 'scancodetemplate',
          name: 'scancodetemplate',
          component: scancodetemplate
        }
      ]
    },
    {
      path: '/appmailletter',
      name: 'AppMailLetter',
      component: AppMailLetter,
      children: [
        { path: 'mailtemplate',
          name: 'mailtemplate',
          component: mailtemplate
        }

      ]

    },
    {
      path: '/qarelease',
      name: 'qarelease',
      component: qarelease
    }
  ]
})



